.loader::after {
  --width: 1em;
  --height: 1em;
  content: "";
  position: absolute;
  top: 100%;
  left: calc(50% - var(--width)/2);
  width: 1em;
  height: 1em;
  background-color: rgb(44, 0, 114);
  box-shadow: inset 5px 5px 10px rgb(160, 120, 255),
  0px 0px 2px white;
  border-radius: 50px;
  animation: 1.2s ease-in 1s alternate infinite running jump2;
}

@keyframes first682 {
  0% {
    transform: rotate(0deg);
  }

  25% {
    transform: rotate(0deg);
    transform: rotate(90deg)
  }

  50% {
    transform: rotate(90deg);
    transform: rotate(180deg);
  }

  75% {
    transform: rotate(180deg);
    transform: rotate(270deg)
  }

  100% {
    transform: rotate(270deg);
    transform: rotate(360deg);
  }
}

@keyframes jump2 {
  0% {
    top: 100%;
  }

  25% {
    top: 230%
  }

  50% {
    top: 100%;
  }

  75% {
    height: 0.6em
  }

  100% {
    height: 1em
  }
}
/*Hover*/
.card:hover {
  box-shadow: 0 8px 50px #23232333;
}

.card:hover .card-info {
  transform: translateY(-5%);
}

.card:hover .card-social {
  transform: translateY(100%);
  opacity: 1;
}

.card-social__item svg:hover {
  fill: #232323;
  transform: scale(1.1);
}

.pageTwoTransitionOut {
  position: relative;
  top: 100vh;
  z-index: 10;
  fill: rgb(0, 0, 0);
  opacity: 0;
}

.secondPageTitle {
  font-family: Rajdhandi-Regular;
  padding-top: 10px;
  font-size: 10vh;
  width: fit-content;
  position: relative;
  top:0vh;
  text-shadow: 1px 1px 0 white;
}
.prevent-select {
  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10 and IE 11 */
  user-select: none; /* Standard syntax */
}



.secondPage {
  background-color: rgba(0,0,0,0);
  height: 100vh;
  width: 100vw;
  z-index: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  padding-top: -50%;
  z-index: 2;
}


.thirdPage{
  position: relative;
  height: fit-content;
  padding-bottom: 40vh;
  width: 100vw;
  z-index: 10;
  top: 0;
  background-color: #222;
  background: black;
  display: grid;
  column-gap: 50px;
  grid-template-columns: repeat(3, 1fr);
}

.scrollBtn {
  width: 30px;
  height: 50px;
  border-radius: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  border: none;
  outline: 2px solid rgb(135, 105, 255);
  box-shadow: 0px 0px 10px rgb(105, 127, 255);
  position: relative;
  top: 10%;
}

.scroll {
  width: 5px;
  height: 10px;
  border-radius: 10px;
  background-color: rgb(135, 105, 255);
  box-shadow: 0px 0px 10px rgb(135, 105, 255);
  animation: scroll_4013 2s linear infinite;
  transform: translateY(40%);
}

.scrollBtn:after {
  content: 'scroll';
  position: absolute;
  top: 140%;
  color: black;
  text-transform: uppercase;
  letter-spacing: 1.5px;
}

@keyframes scroll_4013 {
  0% {
    transform: translateY(40%);
  }

  50% {
    transform: translateY(90%);
  }
};


